import React, {useEffect} from "react";
import checkmarkicon from "../images/check-mark.png";
import trashicon from "../images/delete.png";
const TaskCard = ({
  setTasks,
  tasks,
  complete,
  setcomplete,
  searchChange,
  setsearchChange
}) => {
  const DeleteTaskHandler = (e, id) => {
    let delTasks = tasks.filter((task, index) => {
      return id !== task.id;
    });
    setTasks(delTasks);

  };

  const CompleteTaskHandler = (e, id) => {
   
    let compTasks = tasks.filter((task, index) => {
      return id === task.id;
    });
    setcomplete(!complete);
    compTasks[0].isCompleted = !complete;
  };
  useEffect(() => {
    localStorage.setItem('Todo', JSON.stringify([...tasks]))
  }, [complete])

  return (
    <div className="task-lists">
      {tasks.map((task, i) => {
        const { id, title, description, isCompleted } = task;
        return (
          
          <div
            className={`task-card flex ${isCompleted ? "complete" : ""} ${title.indexOf(searchChange) !== -1 || searchChange === "" ? '' : 'd-none'}`}
            key={i}
          >
            <div className="task-context">
              <h3 className="title">{title}</h3>
              <p>{description}</p>
              <p>
                Status: <span>{isCompleted ? 'Completed' : 'TBI'}</span>
              </p>
            </div>
            <figure className="task-complete">
              <img
                src={checkmarkicon}
                alt="checkmarkicon"
                onClick={(e) => CompleteTaskHandler(e, id)}
              />
            </figure>
            <figure className="task-delete">
              <img
                src={trashicon}
                alt="trashicon"
                onClick={(e) => DeleteTaskHandler(e, id)}
              />
            </figure>
          </div>
        );
      })}
    </div>
  );
};
export default TaskCard;
