import React, { useState, useEffect } from "react";
import addIcon from "../images/add.png";
const AddTodo = ({ popup, setPopup, searchChange,setsearchChange }) => {

  const PopupOpen = (e) => {
    setPopup(true);
  };

  return (
    
    <div className="add-task flex">
      <p className="flex">
        New Task
        <span className="add-icon">
          <img src={addIcon} onClick={PopupOpen} />
        </span>
      </p>
      <input
        type="text"
        placeholder="Search Tasks"
        className="search-todo"
        onChange={e => setsearchChange(e.target.value.toLowerCase())}
        value={searchChange}
      />
    </div>
   
  );
};
export default AddTodo;
