import React, { useState, useEffect } from "react";
import { v4 as uuidv4 } from "uuid";
import closeImg from "../images/cancel.png";


const TodoPopup = ({ popup, setPopup, tasks, setTasks, complete, setsearchChange }) => {
  const ClosePopup = () => {
    setPopup(false);
  };

  const [titleText, settitleText] = useState("");
  const [descText, setdescText] = useState("");

  const TitleTextHandler = (e) => {
    settitleText(e.target.value);
  };
  const DescTextHandler = (e) => {
    setdescText(e.target.value);
  };

  const TaskSubmitHandler = (e) => {
    e.preventDefault();
    setTasks([
      ...tasks,
      {
        id: uuidv4(),
        title: titleText,
        description: descText,
        isCompleted: complete,
      },
    ]);

    settitleText("");
    setdescText("");
    setsearchChange('')
  };


  useEffect(() => {
    let localTodos = JSON.parse(localStorage.getItem('Todo'))

    if (localStorage.getItem("Todo")) {
      setTasks(localTodos);
    }
  }, [])

  useEffect(() => {
    localStorage.setItem('Todo', JSON.stringify([...tasks]))
  }, [tasks])

  return (
    <>
      <div className={`todo-popup ${popup ? "d-block" : ''}`}>
        <div className="popup-container">
          <figure className="text-right">
            <img
              src={closeImg}
              alt="close"
              className="popup-close"
              onClick={ClosePopup}
            />
          </figure>
          <h2>Add a Task</h2>
          <form onSubmit={TaskSubmitHandler}>
            <input
              type="text"
              placeholder="Title"
              onChange={TitleTextHandler}
              value={titleText}
            />
            <textarea
              name="description"
              id="description"
              cols="30"
              rows="10"
              placeholder="Task Description"
              onChange={DescTextHandler}
              value={descText}
            ></textarea>
            <button className="popup-btn" type="submit">
              Add Task
            </button>
          </form>
        </div>
      </div>
      <div className={`overlay ${popup ? "d-block" : ''}`}></div>
    </>
  );
};
export default TodoPopup;
